import {model} from "./model";
import * as Functions from "./templates"
import "./styles/main.css"

const $site = document.querySelector('#site');

model.forEach(block => {
    let html = '';
    let arrayOfColumns = [];

    if (block.type === 'title') {
        html = Functions.title(block);
    }
    else if (block.type === 'text') {
        html = Functions.text(block);
    }
    else if (block.type === 'columns') {
        html = Functions.columns(block);
    }
    else if (block.type === 'image') {
        html = Functions.image(block);
    }

    $site.insertAdjacentHTML('beforeend', html)
});
